@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap');

:root {
  /* main color */
  --dark: rgb(28, 33, 40);
  --dark-bg: rgb(34, 39, 47);
  --grey-bg: rgb(55, 62, 71);

  /* text color */
  --white-color: #f2f4f3;
  --grey-color: rgb(173, 186, 199);

  /* padding */
  --general-padding: 0px 15px 0px 15px;
  --text-padding: 2px 10px 2px 10px;

  /* margin */
  --center-margin-small: 5px auto 5px auto;
  --center-margin-medium: 10px auto 10px auto;
  --center-margin-large: 20px auto 20px auto;

  /* border */
  --border: 1px solid rgb(68, 77, 86);
  --border-radius: 15px;
  --border-radius-rounder: 25px;
}

html,
body {
  margin: 0;
  background-color: var(--dark);
  font-family: -apple-system, BlinkMacSystemFont, 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/***************** Card *****************/
.Card {
  width: clamp(350px, 50%, 450px);
  background-color: var(--dark-bg);
  border: var(--border);
  border-radius: var(--border-radius);
  color: var(--white-color);
  padding-top: 20px;
  margin: 4em auto 4em auto;
}

.Card .center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#avatar {
  height: auto;
  width: auto;
  max-width: 200px;
  max-height: 200px;
  align-self: center;
  background-color: var(--grey-bg);
  border-radius: var(--border-radius-rounder);
}

#name {
  color: var(--white-color);
  font-size: 35px;
  font-weight: 500;
  padding: var(--text-padding);
  margin: var(--center-margin-small);
  text-align: center;
}

#title {
  color: var(--white-color);
  text-align: center;
  margin: 0px auto 0px auto;
  padding: var(--text-padding);
  background-color: var(--grey-bg);
  border: var(--border);
  border-radius: var(--border-radius);
  user-select: none;
}

/***************** hr *****************/
.hr {
  width: 70%;
  background-color: var(--grey-color);
  opacity: 0.5;
  border: none;
  height: 1px;
  margin: var(--center-margin-medium);
}

/***************** Info-card *****************/
.Info-card {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.Info-card .logo {
  padding: 10px;
  margin-right: 10px;
  border-radius: var(--border-radius);
  border: var(--border);
}

.Info-card .info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5px;
}

.Info-card .title {
  color: var(--grey-color);
  user-select: none;
}

/***************** grid *****************/
.container {
  display: grid;
  grid-template-columns: auto;
  width: 70%;
  margin: 10px auto 0px auto;
}

.grid-item {
  margin: 0px 0px 10px 5px;
}

.social-media-container {
  display: grid;
  grid-auto-flow: column;
  align-self: center;
  text-align: end;
  width: 70%;
  margin: 0px auto 0px auto;
}

.media-icons {
  font-size: 3.2em;
  height: 50px;
  overflow: hidden;
}

.i {
  position: relative;
  top: 30px;
  transition: all 100ms cubic-bezier(0.5, 1.5, 0.5, 1);
}
.i:hover {
  top: 0px;
  cursor: pointer;
}

.linkedin {
  color: #0072b1;
}
